footer {
  padding: 20px 0;
  display: flex;
  justify-content: center;
  background-color: rgb(236, 236, 236);
  border-top: 1px solid black;
} footer img {
  height: 45px;
} footer h1 {
  font-size: 45px;
  margin: 0 25px;
} .footer-links {
  text-decoration: none;
  color: black;
} 

#linkedin-link, #github-link {
  margin: 0 15px;
  border-radius: 100%;
}

@media screen and (max-width: 945px) {
  footer {
    flex-direction: column;
    display: flex;
    justify-content: center;
  } .footer-links {
    display: flex;
    justify-content: center;
  }
}

@media screen and (max-width: 545px) {
  .footer-links h1, .footer-links {
    font-size: 30px;
  }
}